.navigation-item {
  margin: 10px 0;
  font-size: clamp(25px, 5vw, 120px);
  cursor: pointer;
  text-decoration: none;
  color: white;
  position: relative;
  transition: all 0.2s linear;
  text-transform: uppercase;
  &::before {
    content: '';
    width: 0;
    height: 2px;
    position: absolute;
    bottom: 1px;
    background-color: white;
    transition: all 0.3s linear;
  }

  &:hover::before {
    width: 100%;
  }
}

.navigation-item-active {
  color: white;
}
